import interfaces from '../../utils/interfaces.js';
import app from "../../App.vue";
var QQMapWX = require('../../utils/qqmap-wx-jssdk.min.js');
var qqmapsdk;
export default {
	data() {
		return {
			mapCtx: '',
			user_info:'',

			latitude: 23.099994,
			longitude: 113.324520,
			dangqian: '',
			markers: [{
				id: 1,
				latitude: 23.099994,
				longitude: 113.324520,
				name: 'T.I.T 创意园'
			}],
		}
	},
	/**
	 * 生命周期函数--监听页面初次渲染完成
	 */
	onReady() {
		this.mapCtx = wx.createMapContext('myMap')
	},

	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow() {
		this.user_info=app.globalData.user_info;
		if(this.user_info==""){
			wx.reLaunch({
				url: '/pages/index/index',
			})
		}else{
			
			// 实例化API核心类
			qqmapsdk = new QQMapWX({
				key: 'WCYBZ-EWVC4-CC3U2-DZGZI-UWGK2-E2BMM'
			});
			wx.getLocation({
				type: 'gcj02',
				success: res => {
					qqmapsdk.reverseGeocoder({
						location: {
							latitude: res.latitude,
							longitude: res.longitude
						},
						success: addressRes => {
							this.latitude = res.latitude;
							this.longitude = res.longitude;
							this.dangqian = addressRes.result.address_component.city;
							this.master();
						}
					})
				},
				fail: err => {
					//console.log(err);
					if (err.errMsg === "getLocation:fail auth deny") {
						wx.showModal({
							title: '提示',
							showCancel: false,
							confirmText: '好的',
							content: '未获取到你的地理位置，暂时无法为你提供服务。请检查是否开启定位权限。或尝试重新打开小程序。',
							success: (res) => {
								if (res.confirm) {
									wx.openSetting({
										success(settingdata) {
											//console.log(settingdata)
											if (settingdata.authSetting['scope.userLocation']) {
												wx.showToast({
													title: '授权成功',
													icon: 'none',
													mask: true,
												})
											} else {
												wx.showToast({
													title: '授权失败',
													icon: 'none',
													mask: true,
												})
											}
										}
									})
								}
							}
						})
					}
				}
			});
			
		}
	},
	methods: {
		//获取所有师傅定位列表
		master() {
			this.request({
				url: interfaces.locationList,
				method: 'POST',
				success: res => {
					console.log(res);
					if(res.code==200){
						var markers = res.data;
						var location = {
							title: '当前位置',
							latitude: this.latitude,
							longitude: this.longitude,
							width: 30,
							height: 30,
							iconPath: '/static/images/biaodian.png'
						}
						markers.push(location);
						this.markers = markers;
					}
				}
			})
		},

		//获取当前中心点的经纬度
		goOrder() {
			this.mapCtx.getCenterLocation({
				success: function(res) {
					wx.navigateTo({
						url: '../order/add'
					})
				}
			})
		},
		//移到自己定位坐标
		moveToLocation() {
			this.mapCtx.moveToLocation();
		},
	}
}
